/**
 * 过滤器
 */

import Vue from 'vue';

/**
 * 时间戳转时间类型
 * @param  string   type 需要转换的时间类型：date--转换成日期；time--转换成时间；默认为日期+时间
 */
Vue.filter('dateFormat', function(val,type) {
    if (val) {
    	var date=new Date(val).toLocaleDateString().replace(/\//g,'-');
    	var time=new Date(val).toTimeString().substring(0,8);
    	if(type==='date'){
    		return date;
    	}else if(type==='time'){
    		return time;
    	}else{
    		return date+' '+time;
    	}
    }
    return "-";
});

/**
 * 毫秒转mm:ss
 */
Vue.filter('timeFormat',function(val){
	if(val){
		var time=parseInt(val/1000);  //将毫秒转成秒
		var second=time%60<10?'0'+time%60:time%60;  //秒
		var minute=parseInt(time/60)<10?'0'+parseInt(time/60):parseInt(time/60); //分
		return minute+':'+second;
	}
	return '';
})

/**
 * 文件路径转文件名
 */
Vue.filter('fileName',function(val){
	if(val){
		var index=val.lastIndexOf('/');
		return val.substring(index+1);
	}
	return '';
})

/**
 * 子任务文件类型
 */
Vue.filter('taskType',function(val){
	if(val==='LightingFile'){
		return '灯光文件';
	}else if(val==='VideoFile'){
		return '视频文件';
	}else{
		return '其它类型';
	}
})

/**
 * 定时任务模式
 */
Vue.filter('taskMode',function(val){
	if(val==='EventMode'){
        return '事件模式';
	}else if(val==='NormalMode'){
		return '常规模式';
	}else if(val==='VacationMode'){
		return '假期模式';
	}
})

/**
 * 定时任务循环模式
 */
Vue.filter('taskLoopMode',function(val){
	if(val==='LoopFree'){
		return '无循环';
	}else if(val==='Everyday'){
		return '每天';
	}else if(val==='Weekly'){
		return '每周';
	}else if(val==='Monthly'){
		return '每月';
	}
})



