<template>
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true">
        <el-form-item>
          <el-input placeholder="任务名"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary">查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd">新增</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!--列表-->
    <el-table :data="taskList" highlight-current-row style="width: 100%;">
      <el-table-column type="selection"></el-table-column>
      <el-table-column type="index"></el-table-column>
      <el-table-column label="任务名称" prop="taskName"></el-table-column>
      <el-table-column label="任务模式">
        <template slot-scope="scope">{{scope.row.taskType | taskMode}}</template>
      </el-table-column>
      <el-table-column label="循环模式">
        <template slot-scope="scope">{{scope.row.cyclicMode | taskLoopMode}}</template>
      </el-table-column>
      <el-table-column label="循环日期">
        <template slot-scope="scope">{{scope.row.cyclicDate?scope.row.cyclicDate:'-'}}</template>
      </el-table-column>
      <el-table-column label="开始日期" prop="startDate">
        <template slot-scope="scope">{{scope.row.startDate | dateFormat('date')}}</template>
      </el-table-column>
      <el-table-column label="结束日期" prop="endDate">
        <template slot-scope="scope">{{scope.row.endDate | dateFormat('date')}}</template>
      </el-table-column>
      <el-table-column label="开始时间" prop="startTime">
        <template slot-scope="scope">{{scope.row.startTime | dateFormat('time')}}</template>
      </el-table-column>
      <el-table-column label="结束时间" prop="endTime">
        <template slot-scope="scope">{{scope.row.endTime | dateFormat('time')}}</template>
      </el-table-column>
      <el-table-column label="播放列表" prop="subtaskNames"></el-table-column>
      <el-table-column label="操作">
        <template scope="scope">
          <el-button type="primary">编辑</el-button>
          <el-button type="danger">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-button type="danger">批量删除</el-button>
      <el-button type="warning" @click="sendPauseTask">暂停任务</el-button>
      <el-button type="primary" @click="sendRestoreTask">恢复任务</el-button>
      <el-pagination layout="prev, pager, next" :page-size="20" :total="taskList.length" style="float:right;"></el-pagination>
    </el-col>
    <!--新增界面-->
    <el-dialog title="添加定时任务" :visible.sync="addFormVisible" :close-on-click-modal="false">
      <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-form-item label="任务名称" prop="taskName">
          <el-input v-model="addForm.taskName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="播放列表" prop="subtaskIds">
          <el-select v-model="addForm.subtaskIds" :collapse-tags="true" multiple placeholder="请选择">
            <el-option v-for="item in playListOptions" :key="item.subtaskId" :label="item.subtaskName" :value="item.subtaskId"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="任务模式" required>
          <el-radio-group v-model="addForm.type">
            <el-radio class="radio" :label="0">常规模式</el-radio>
            <el-radio class="radio" :label="1">假日模式</el-radio>
            <el-radio class="radio" :label="2">事件模式</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="起止日期" prop="date">
          <el-date-picker v-model="addForm.date" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="执行时间" required>
          <el-col :span="11">
            <el-form-item prop="startTime">
              <el-time-picker v-model="addForm.startTime" placeholder="开始时间" style="width:100%;"></el-time-picker>
            </el-form-item>
          </el-col>
          <el-col :span="2" style="text-align: center;">至</el-col>
          <el-col :span="11">
            <el-form-item prop="endTime">
              <el-time-picker v-model="addForm.endTime" placeholder="结束时间" style="width:100%;"></el-time-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="循环模式">
          <el-switch v-model="addForm.cyclic" active-color="#13ce66" inactive-color="#ff4949" @change="cyclicSwitch"></el-switch>
        </el-form-item>
        <el-form-item label="循环类型" prop="cyclicMode">
          <el-radio-group v-model="addForm.cyclicMode" :disabled="!addForm.cyclic">
            <el-radio class="radio-block" :label="1">每天</el-radio>
            <el-radio class="radio-block" :label="2">
              每周
              <el-select v-model="addForm.cyclicWeeks" :collapse-tags="true" :disabled="addForm.cyclicMode!==2" multiple placeholder="请选择">
                <el-option v-for="item in weeks" :key="item.value" :label="item.label" :value="item.value"></el-option>
              </el-select>
            </el-radio>
            <el-radio class="radio-block" :label="3">
              每月
              <el-date-picker type="dates" popper-class="chooseDates" :disabled="addForm.cyclicMode!==3" format="dd" v-model="addForm.cyclicDates" default-value="2018/7/1" placeholder="选择一个或多个日期">
              </el-date-picker>
            </el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false">取消</el-button>
        <el-button type="primary" @click.native="addSubmit('addForm')">提交</el-button>
      </div>
    </el-dialog>
  </section>
</template>
<script>
import { getPlayList, getTaskList, addTimedTask, querySpiritStatus, pauseTask, restoreTask, checkTaskName } from '@/api/api.js'

export default {
  data() {
    var checkName = (rule, value, callback) => {
      if (value) {
        checkTaskName({ taskName: value }).then(res => {
          if (res.result === 'true') {
            callback();
          } else {
            callback(new Error('任务名称已存在'));
          }
        })
      }
    };
    var checkMode=(rule,value,callback)=>{
      if(this.addForm.cyclic){
        if(this.addForm.cyclicMode===0){
          callback(new Error('请选择循环类型'));
        }else if(this.addForm.cyclicMode===1){
          callback();
        }else if(this.addForm.cyclicMode===2){
          if(this.addForm.cyclicWeeks.length>0){
            callback();
          }else{
            callback(new Error('请选择循环时间'));
          }
        }else if(this.addForm.cyclicMode===3){
          if(this.addForm.cyclicDates){
            callback();
          }else{
            callback(new Error('请选择循环时间'));
          }
        }
      }else{
        callback();
      }
    }
    return {
      taskList: [],
      addFormVisible: false, //新增界面是否显示
      //新增界面数据
      addForm: {
        taskName: '',
        type: 0,
        date: '',
        startTime: '',
        endTime: '',
        cyclic: false,
        cyclicMode: 0,
        cyclicDates: '',
        cyclicWeeks: '',
        subtaskIds: ''
      },
      weeks: [{
        value: 1,
        label: '星期一'
      }, {
        value: 2,
        label: '星期二'
      }, {
        value: 3,
        label: '星期三'
      }, {
        value: 4,
        label: '星期四'
      }, {
        value: 5,
        label: '星期五'
      }, {
        value: 6,
        label: '星期六'
      }, {
        value: 7,
        label: '星期日'
      }],
      addFormRules: {
        taskName: [
          { required: true, message: '请输入定时任务名称', trigger: 'blur' },
          { validator: checkName, trigger: 'blur' }
        ],
        subtaskIds: [
          { required: true, message: '请至少选择一个子任务', trigger: 'change' }
        ],
        date: [
          { required: true, message: '请选择起止日期', trigger: 'change' }
        ],
        startTime: [
          { required: true, message: '请选择开始时间', trigger: 'change' }
        ],
        endTime: [
          { required: true, message: '请选择结束时间', trigger: 'change' }
        ],
        cyclicMode:[
          { validator: checkMode, trigger: 'change' }
        ]
      }
    }
  },
  computed: {
    //是否跟新定时任务列表
    ifUpdateTaskList:function(){
      return this.$store.state.ifUpdateTimedTaskList;
    },
    //播放列表选项
    playListOptions: function() {
      return this.$store.state.subtasksList;
    },
    taskName: function() {
      return this.addForm.taskName;
    },
    type: function() {
      return this.addForm.type
    },
    mode: function() {
      return this.addForm.cyclicMode
    },
    cyclicDate: function() {
      var cyclicDate = '';
      if (this.addForm.cyclicMode === 0 || this.addForm.cyclicMode === 1) {
        cyclicDate = '';
      } else if (this.addForm.cyclicMode === 2) {
        cyclicDate = this.addForm.cyclicWeeks.join(',');
      } else if (this.addForm.cyclicMode === 3) {
        for (var i = 0; i < this.addForm.cyclicDates.length; i++) {
          if (this.addForm.cyclicDates) {
            this.addForm.cyclicDates[i] = this.addForm.cyclicDates[i].getDate()
          }
        }
        cyclicDate = this.addForm.cyclicDates.join(',');
      }
      return cyclicDate;
    },
    strStartDate: function() {
      var date = this.addForm.date[0];
      return new Date(date).getTime();
    },
    strEndDate: function() {
      var date = this.addForm.date[1];
      return new Date(date).getTime();
    },
    strStartTime: function() {
      var time = this.addForm.startTime;
      return new Date(time).toTimeString().substring(0, 8);
    },
    strEndTime: function() {
      var time = this.addForm.endTime;
      return new Date(time).toTimeString().substring(0, 8);
    },
    subtaskIds: function() {
      return this.addForm.subtaskIds.join(',');
    },
    params: function() {
      var data = {
        taskName: this.taskName,
        type: this.type,
        mode: this.mode,
        cyclicDate: this.cyclicDate,
        strStartTime: this.strStartTime,
        strEndTime: this.strEndTime,
        startTimestamp: this.strStartDate,
        endTimestamp: this.strEndDate,
        subtaskIds: this.subtaskIds
      }
      return data;
    }
  },
  methods: {
    //获取子任务列表
    getSubtaskList() {
      const that = this;
      getPlayList().then(res => {
        console.log(res)
        that.$store.commit('updateSubtasksList', res.subtasksList);
      })
    },

    //获取定时任务列表
    getTaskList() {
      const that = this;
      getTaskList().then(res => {
        console.log(res)
        that.taskList = res.tasksList;
      })
    },

    //显示编辑界面
    handleEdit: function(index, row) {
      this.editFormVisible = true;
      this.editForm = Object.assign({}, row);
    },
    //显示新增界面
    handleAdd: function() {
      this.addFormVisible = true;
    },
    addSubmit(formName) {
      const that = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          addTimedTask(that.params).then(res => {
            console.log(res);
            if (res.result === 'send successful') {
              that.$message.success('添加任务成功!');
              that.addFormVisible = false;
            }
          });
        } else {
          return false;
        }
      })
    },
    cyclicSwitch(val) {
      if (!val) {
        this.addForm.cyclicMode = 0;
      }
    },

    //暂停任务
    sendPauseTask() {
      pauseTask().then(res => { console.log(res) })
    },

    //恢复任务
    sendRestoreTask() {
      restoreTask().then(res => { console.log(res) })
    }
  },
  created() {
    this.getTaskList();
    this.getSubtaskList();
  },
  watch:{
    ifUpdateTaskList(val){
      if(val){
        this.getTaskList();
        this.$store.commit('updateTimedTaskList',false);
      }else{
        return;
      }
    }
  }
}

</script>
<style scoped>
.radio-block {
  display: block;
  padding: 10px;
}

</style>



