import store from '@/vuex/store'

var webSocketErr = 0;
var timer;
var ws;

export function createWebSocket(context) {
    //新建一个websocket连接
    ws = new WebSocket('ws://192.168.100.100:8081/lovdmx_control/websocket/socketServer');

    //websocket连接
    ws.onopen = function() {
        //连接成功，将错误计数清零,清除定时器
        webSocketErr = 0;
        clearTimeout(timer);

        console.log('连接成功');

    }

    //客户端接收服务端的数据
    ws.onmessage = function(res) {
        console.log(JSON.parse(res.data));
        switch(JSON.parse(res.data).opCode){
            case 2222:
                context.$store.commit('initList',JSON.parse(res.data));
                break;
            case 1111:
                if(JSON.parse(res.data).solveDalarmDataList){
                    for(var i=0;i<JSON.parse(res.data).solveDalarmDataList.length;i++){
                        for(var k=0;k<context.$store.state.warningInfoList.length;k++){
                            if(JSON.parse(res.data).solveDalarmDataList[i].errId===context.$store.state.warningInfoList[k].errId){
                                var item={};
                                for(var key in context.$store.state.warningInfoList[k]){
                                    item[key]=context.$store.state.warningInfoList[k][key]
                                }
                                item.resolutionState=1;
                                context.$store.state.warningInfoList.unshift(item);
                                context.$notify({
                                    title:'提示',
                                    message:'警报已解除：'+item.rackName+item.errDescribe,
                                    type:'success',
                                    duration:0
                                });
                                break;
                            }
                        }
                    }
                }else if(JSON.parse(res.data).addAlarmDataList){
                    for(var i=0;i<JSON.parse(res.data).addAlarmDataList.length;i++){
                        context.$store.state.warningInfoList.unshift(JSON.parse(res.data).addAlarmDataList[i]);
                        context.$notify({
                            title:'提示',
                            message:'警报：'+JSON.parse(res.data).addAlarmDataList[i].rackName+JSON.parse(res.data).addAlarmDataList[i].errDescribe,
                            type:'warning',
                            duration:0
                        })
                    }
                }
                context.$store.state.ifUpdateWarningList=true;
                break;
            case 4134:
                if(JSON.parse(res.data).status===1){
                    context.$message.success('上传成功！');
                    context.$store.commit('updateFileList',JSON.parse(JSON.parse(res.data).dataJson));
                }else if(JSON.parse(res.data).status===-1){
                    context.$message.warning('文件路径错误！');
                }else if(JSON.parse(res.data).status===2){
                    context.$message.warning('文件已存在！');
                }else{
                    context.$message.error('上传失败！');
                }
                break;
            case 4102:
                
                break;
            case 4103:
                break;
            case 4104:
                if(JSON.parse(res.data).status==='1'){
                    context.$store.commit('updateTimedTaskList',true); 
                }
                break;
        }
    }

    //通信发生错误
    ws.onerror = function() {
        //错误计数webSocketErr+1
        //关闭webSocket连接
        //重新创建webSocket连接
        //如果连续错误5次以上，停止连接，并提示用户
        webSocketErr++;
        ws.close();
        if (webSocketErr > 5) {
            alert('与服务器连接失败！');
        } else {
            timer = setTimeout(function() {
                createWebSocket();
            }, 1000)
        }
    }

    //通信断开
    ws.onclose=function(){
        console.log('websocket断开连接了')
    }
};



