<template>
  <section>
    <div class="back">
      <el-button type="text" icon="el-icon-arrow-left" @click="$router.go(-1)">返回</el-button>
    </div>
    <!-- 继电器模块列表 -->
    <el-table :data="nodeList">
      <el-table-column align="center" type="index"></el-table-column>
      <el-table-column align="center" label="ip" prop="rs485toNetIp"></el-table-column>
      <el-table-column align="center" label="从站地址" prop="slaveAddr"></el-table-column>
      <el-table-column align="center" label="回路数量" prop="number"></el-table-column>
      <el-table-column align="center" label="回路状态">
        <template slot-scope="scope">
          <relayStatus :onList="scope.row.electricRelayLoopON" :offList="scope.row.electricRelayLoopOFF"></relayStatus>
        </template>
      </el-table-column>
      <el-table-column align="center" label="回路控制">
        <template slot-scope="scope">
          <el-button type="primary" size="small" @click="setInstantTask(scope.row)">手动控制</el-button>
          <el-button type="primary" size="small" @click="setTimerTask(scope.row)">自动控制</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 手动控制页面 -->
    <el-dialog title="手动控制" :visible.sync="instantTaskVisible" center width="500px">
      <el-table :data="loopList" @selection-change="selectionChange">
        <el-table-column align="center" type="selection"></el-table-column>
        <el-table-column align="center" type="index"></el-table-column>
        <el-table-column align="center" label="回路状态">
          <template slot-scope="scope">
            <el-button circle :type="scope.row.value?'success':'danger'"></el-button>
          </template>
        </el-table-column>
        <el-table-column align="center" label="回路控制">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.value" active-color="#13ce66" inactive-color="#ff4949" @change="changeLoop(scope.row)"></el-switch>
          </template>
        </el-table-column>
      </el-table>
      <div slot="footer" class="dialog-footer">
        <el-button type="success" @click="allOpend">批量开启</el-button>
        <el-button type="danger" @click="allClose">批量关闭</el-button>
      </div>
    </el-dialog>
    <!-- 自动控制页面 -->
    <!-- <el-dialog title="自动控制" :visible.sync="timerTaskVisible" center width="600px">
      <el-form label-width="80px" label-position="left">
        <el-form-item v-for="item in timerList" :label="item.wWeek">
          <div v-if="item.taskList.length>0">
            <div v-for="itm in item.taskList">
              <el-time-picker placeholder="开启时间" v-model="itm.startTime"></el-time-picker>
              -
              <el-time-picker placeholder="关闭时间" v-model="itm.endTime"></el-time-picker>
            </div>
          </div>
          <div v-else>
            <el-time-picker placeholder="开启时间" v-model="item.startTime"></el-time-picker>
            -
            <el-time-picker placeholder="关闭时间" v-model="item.endTime"></el-time-picker>
          </div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="timerTaskVisible = false">取 消</el-button>
        <el-button type="primary" @click="timerTaskSubmit">确 定</el-button>
      </div>
    </el-dialog> -->
  </section>
</template>
<script>
import relayStatus from '@/components/relayStatus.vue'
import { getNodeList, getGatewayTimerTask, sendInstantTask } from '@/api/api.js'
export default {
  data() {
    return {
      nodeList: [], //继电器列表
      // timerList: [],
      instantTaskVisible: false, //是否显示即时任务页面
      timerTaskVisible: false, //是否显示定时任务页面
      loopList: [],
      selectedLoop: '',
      instantForm: {
        rackId: '',
        saveState: false, //是否保存任务，此项固定为false，不保存
        taskName: '', //任务名称，此项固定为空
        taskMsg: {
          delaymsc: '500', //延时时间，此项固定为500毫秒
          realTimeTasks: [{
            rs485toNetIP: '',
            port: '',
            tasks: [{
              slaveAddr: '',
              loopNums: '',
              state: ''
            }]
          }]
        }
      },
      timerForm: {

      }
    }
  },
  components: {
    relayStatus
  },
  methods: {
    //获取节点列表
    getNodeList(mac) {
      const that = this;
      getNodeList({ gatewayMac: mac }).then(res => {
        if (res.result === 'true') {
          that.nodeList = res.relayList;
        }
      })
    },

    //显示即时任务页面
    setInstantTask(obj) {
      this.instantTaskVisible = true;
      var trueList = obj.electricRelayLoopON.split(',');
      var falseList = obj.electricRelayLoopOFF.split(',');
      var arr = [];
      for (var i = 0; i < trueList.length; i++) {
        arr[parseInt(trueList[i])] = {};
        arr[parseInt(trueList[i])].index = trueList[i];
        arr[parseInt(trueList[i])].value = true;
      }
      for (var i = 0; i < falseList.length; i++) {
        arr[parseInt(falseList[i])] = {};
        arr[parseInt(falseList[i])].index = falseList[i];
        arr[parseInt(falseList[i])].value = false;
      }
      this.loopList = arr;
      this.instantForm.taskMsg.realTimeTasks[0].rs485toNetIP = obj.rs485toNetIp;
      this.instantForm.taskMsg.realTimeTasks[0].port = obj.port;
      this.instantForm.taskMsg.realTimeTasks[0].tasks[0].slaveAddr = obj.slaveAddr;
    },

    //点击回路的开关来控制回路的开启和关闭
    changeLoop(obj) {
      console.log(obj)
      this.instantForm.taskMsg.realTimeTasks[0].tasks[0].loopNums = obj.index;
      this.instantForm.taskMsg.realTimeTasks[0].tasks[0].state = obj.value ? '1' : '0';
      this.sendInstantTask();
    },

    //选择回路
    selectionChange(selection) {
      var arr = [];
      for (var i = 0; i < selection.length; i++) {
        arr.push(selection[i].index);
      }
      this.selectedLoop = arr.join(',');
    },

    //开启选中的回路
    allOpend() {
      if (this.selectedLoop !== '') {
        this.instantForm.taskMsg.realTimeTasks[0].tasks[0].loopNums = this.selectedLoop;
        this.instantForm.taskMsg.realTimeTasks[0].tasks[0].state = '1';
        this.sendInstantTask();
      }
    },

    //关闭选中的回路
    allClose() {
      if (this.selectedLoop !== '') {
        this.instantForm.taskMsg.realTimeTasks[0].tasks[0].loopNums = this.selectedLoop;
        this.instantForm.taskMsg.realTimeTasks[0].tasks[0].state = '0';
        this.sendInstantTask();
      }
    },

    //发送即时任务
    sendInstantTask() {
      sendInstantTask(this.instantForm).then(res => { console.log(res) })
    },
    //获取定时任务
    getGatewayTimerTask(id) {
      const that = this;
      getGatewayTimerTask({ relayId: id }).then(res => {
        console.log(res)
        if (res.result === 'true') {
          for (var i = 0; i < res.weekList.length; i++) {
            res.weekList[i].taskList = [];
            for (var k = 0; k < res.relayTasksList.length; k++) {
              if (res.relayTasksList[k].weekId === res.weekList[i].wId) {
                res.weekList[i].taskList.push(res.relayTasksList[k]);
              }
            }
          }
          that.timerList = res.weekList;
        }
      })
    },
    //设置定时任务
    setTimerTask(obj) {
      this.timerTaskVisible = true;
      this.getGatewayTimerTask(obj.id);
    },
    //提交定时任务
    timerTaskSubmit() {

    }
  },
  created() {
    var mac = this.$route.query.mac;
    var rackId = this.$route.query.rackId + '';
    this.instantForm.rackId = rackId;
    this.getNodeList(mac);
  }
}

</script>
<style></style>



