import axios from 'axios';
import router from '@/router/index.js';
import { Message } from 'element-ui';

//配置axios
axios.defaults.baseURL = 'http://192.168.100.100:8081/lovdmx_control/';
axios.defaults.withCredentials = true;

/**
 * axios 响应拦截器
 * @param {object} response 从服务端响应的数据对象或者error对象
 * @return {object} 响应成功或失败时返回的响应对象或者promise error对象
 **/
axios.interceptors.response.use(response => {
  if (response.data.result === 'false') {
    Message.error('账号已过期或异常，请重新登录');
    router.push({path:'/login'});
  } else {
    return response.data;
  }
}, error => {
  router.push({ path: '/404' });
  return Promise.resolve(error.response)
})

//登录
export const login = params => { return axios.post('login', params) };

//获取录放精灵文件列表
export const getFileList = params => { return axios.get('webControlServer/getDirectoryDataList', { params: params, responseType: 'json' }) };

//获取播放列表
export const getPlayList = () => { return axios.get('webControlServer/getSubtasksDataList') };

//获取录放精灵任务列表
export const getTaskList = () => { return axios.get('webControlServer/getTasksDataList') };

//将主录放精灵的文件上传到公共网盘
export const uploadFile = params => { return axios.get('spriteServer/uploadSpriteFile', { params: params }) };

//分发视频
