<template>
  <el-row type="flex" class="wrap">
    <el-col :span="7" class="content-left">
      <box class="echarts">
        <p class="title">警报统计</p>
        <pieEcharts :pieOptionData="pieOptionData"></pieEcharts>
      </box>
      <box class="echarts">
        <p class="title">警报统计</p>
        <lineEcharts :date="dateList" :optionData="dataList"></lineEcharts>
      </box>
      <box class="warning-info">
        <p class="title">警报信息</p>
        <div style="width:100%;height:calc(100% - 30px);position:relative;">
          <div class="overflow-box" style="position: absolute;left:0;top:0;right:0;bottom:0;overflow: auto;">
            <div class="list-item" style="width:100%;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;margin:5px auto;" v-for="item in warningInfoList">
              <el-tag type="danger" v-if="item.resolutionState===0">警报</el-tag>
              <el-tag type="success" v-if="item.resolutionState===1">已解除</el-tag>
              <el-tag type="info">{{item.rackName}}</el-tag>
              <i> {{item.errDescribe}} </i>
            </div>
          </div>
        </div>
      </box>
    </el-col>
    <el-col :span="9" class="content-middle">
      <box class="map">
        <p class="title">项目分布</p>
        <vueMap></vueMap>
      </box>
      <box class="player">
        <p class="title">播放控制</p>
      </box>
    </el-col>
    <el-col :span="8" class="content-right">
      <box style="height:33.333333333333%">
        <p class="title">预留功能</p>
      </box>
      <box style="height:33.333333333333%">
        <p class="title">预留功能</p>
      </box>
      <box style="height:33.333333333333%">
        <p class="title">预留功能</p>
      </box>
    </el-col>
  </el-row>
</template>
<script>
import box from '@/components/indexBox'
import vueMap from '@/components/map'
import pieEcharts from '@/components/pieChart'
import lineEcharts from '@/components/lineChart'
import { queryWarnningList } from '@/api/api.js'

export default {
  data() {
    return {
      alarmList: [],
      dateList: [],
      dataList: []
    }
  },
  computed: {
    //饼状图数据
    pieOptionData: function() {
      var spiritNum = 0;
      var rtrNum = 0;
      var controlBoxNum = 0;
      var currentNum = 0;
      var voltageNum = 0;
      for (var i = 0; i < this.alarmList.length; i++) {
        switch (this.alarmList[i].deviceType) {
          case 'Sprite':
            spiritNum++;
            break;
          case 'RTR':
            rtrNum++;
            break;
          case 'DMX512':
            controlBoxNum++;
            break;
          case 'Current':
            currentNum++;
            break;
          case 'Voltage':
            voltageNum++;
            break;
          default:
            return false;
            break;
        }
      }
      var data = [
        { value: spiritNum, name: '录放精灵' },
        { value: rtrNum, name: '服务器' },
        { value: controlBoxNum, name: '控制盒' },
        { value: currentNum, name: '电流' },
        { value: voltageNum, name: '电压' }
      ]
      return data;
    },
    warningInfoList: function() {
      return this.$store.state.warningInfoList;
    },
    ifUpdateWarningList: function() {
      return this.$store.state.ifUpdateWarningList;
    }
  },
  components: {
    box,
    vueMap,
    pieEcharts,
    lineEcharts
  },
  methods: {
    //查询本月警报数据
    queryWarningData() {
      const that = this;
      var now = new Date().toLocaleDateString();
      var index = now.lastIndexOf('/'); //日期最后一个斜杆的位置
      var startTime = new Date(now.substring(0, index) + '/1').getTime();
      var endTime = new Date().getTime();
      var params = {
        startTime: startTime / 1000,
        endTime: endTime / 1000,
        deviceType: '',
        resolutionState: ''
      }
      queryWarnningList(params).then(res => {
        that.alarmList = res.errList;
        that.createLineData(res.alarmDateList, startTime, endTime)
      })
    },
    //生成折线图x、y轴数据
    createLineData(list, startTime, endTime) {
      var data = {};
      for (var i = 0; i < list.length; i++) {
        data[list[i].alarmDate] = list[i].alarmNumber
      }
      var date = startTime;
      var dateList = [];
      var dataList = [];
      while (date <= endTime) {
        var time = new Date(date).getDate() + '日';
        var num = data[date] ? data[date] : 0;
        dateList.push(time);
        dataList.push(num);
        date += 86400000;
      }
      this.dateList = dateList;
      this.dataList = dataList;
    }
  },
  created() {
    this.queryWarningData()
  },
  watch: {
    ifUpdateWarningList(val) {
      if (val) {
        this.queryWarningData();
        this.$store.state.ifUpdateWarningList = false;
      } else {
        return false;
      }
    }
  }
}

</script>
<style scoped>
.wrap {
  height: 100%;
}

.box .title {
  font-size: 16px;
  line-height: 30px;
}

.warning-info {
  height: calc(100% - 764px);
}

.echarts {
  height: 382px;
}

.map {
  height: calc(100% - 260px);
}

.player {
  height: 260px;
}

</style>



