<template>
  <el-container class="wrap">
    <el-header>
      <header-vue></header-vue>
    </el-header>
    <el-container>
      <el-aside width="240px">
        <aside-vue></aside-vue>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
import headerVue from '@/components/header'
import asideVue from '@/components/aside'
import {createWebSocket} from '@/api/websocket.js'
 
export default {
  components: {
    headerVue,
    asideVue
  },
  beforeCreate(){
    createWebSocket(this);
  }
}

</script>
<style>
.wrap {
  width: 100%;
  height: 100%;
}

header {
  background: #409EFF;
}

</style>



