<template>
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true">
        <el-form-item>
          <el-input placeholder="文件名"></el-input>
        </el-form-item>
        <el-form-item>
          <el-select v-model="fileType" placeholder="文件类型">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary">查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-upload ref="upload" :with-credentials="true" action="http://192.168.100.100:8080/lovdmx_control/uploadFile/uploadVideo" :data="videoParams" name="videoFile" :auto-upload="false" :limit="1" :on-change="chooseFile" :on-success="uploadSuccess" :on-error="uploadFail">
            <el-button type="primary">上传文件<i class="el-icon-upload el-icon--right"></i></el-button>
          </el-upload>
        </el-form-item>
      </el-form>
    </el-col>
    <!--列表-->
    <el-table :data="showList" highlight-current-row style="width: 100%;">
      <el-table-column type="index"></el-table-column>
      <el-table-column label="文件名" prop="filePath">
        <template slot-scope="scope">{{scope.row.filePath | fileName}}</template>
      </el-table-column>
      <el-table-column label="时长" prop="time">
        <template slot-scope="scope">{{scope.row.time | timeFormat}}</template>
      </el-table-column>
      <el-table-column label="状态">
        <template slot-scope="scope">
          <el-tag type="warning" v-if="scope.row.status===0">未上传</el-tag>
          <el-tag type="danger" v-if="scope.row.status===1">未分发</el-tag>
          <el-tag type="success" v-if="scope.row.status===2">已分发</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" :disabled="scope.row.status!==0" @click="uploadFile(scope.row)">上传</el-button>
          <el-button size="mini" type="danger" :disabled="scope.row.status!==1" @click="sendVideoSplit(scope.row)">分发</el-button>
          <el-button size="mini" type="success" :disabled="scope.row.status!==2" @click="handleAdd(scope.row)">添加</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination layout="prev, pager, next" :page-size="10" :total="fileType==='light'?lightList.length:videoList.length" style="float:right;">
      </el-pagination>
    </el-col>
    <!-- 新增界面 -->
    <el-dialog title="添加子任务" :visible.sync="addFormVisible" :close-on-click-modal="false" width="500px">
      <el-form :model="addForm">
        <el-form-item label="子任务名称">
          <el-input v-model="addForm.subtaskName" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </section>
</template>
<script>
import Spirit from '@/api/spirit.js'
import { addSubtask, sendVideoSplit, sendEdlmxSplit } from '@/api/api.js'

var spirit = new Spirit('00:CF:E0:3C:52:37')
export default {
  data() {
    return {
      page: 0,
      videoParams: { //上传视频的参数
        minute: '' //视频播放时长
      },
      fileType: 'light', //查询的文件类型 
      options: [{
        value: 'light',
        label: '灯光文件'
      }, {
        value: 'video',
        label: '视频文件'
      }],
      addFormVisible: false, //是否显示添加页面
      addForm: { //添加子任务参数
        subtaskName: '', //子任务名称
        filePath: '', //文件路径
        time: '', //文件播放时长
        md5: '', //文件md5值
        type: '' //文件类型:0--灯光文件；1--视频文件
      }
    }
  },
  computed: {
    fileList: function() {
      return this.$store.state.fileList;
    },
    lightList: function() {
      var lightList = [];
      for (var i = 0; i < this.fileList.length; i++) {
        lightList = lightList.concat(this.fileList[i].lightFileList);
      }
      return lightList;
    },
    videoList: function() {
      var videoList = [];
      for (var i = 0; i < this.fileList.length; i++) {
        videoList = videoList.concat(this.fileList[i].videoFileList);
      }
      return videoList;
    },
    showList: function() {
      var showList = [];
      if (this.fileType === 'light') {
        showList = this.lightList.slice(this.page * 10, (this.page + 1) * 10);
      } else if (this.fileType === 'video') {
        showList = this.videoList.slice(this.page * 10, (this.page + 1) * 10);
      }
      return showList;
    }
  },
  components: {

  },
  methods: {
    //获取文件列表
    getFileList() {
      const that = this
      spirit.getFileLists()
        .then(res => {
          console.log(res)
          if (res.result === 'send successful') {
            var fileList = JSON.parse(res.directoryList);
            console.log(fileList)
            that.$store.commit('updateFileList', fileList);
          } else if (res.result === 'not online') {
            var fileList = [{
              lightFileList: res.uploadEdlmxList,
              videoFileList: res.uploadVideoList
            }];
            that.$store.commit('updateFileList', fileList);
          }
        })
        .catch(res => {
          that.$message.error('服务器发生了一点小问题');
        })
    },

    //选择需要上传的文件
    chooseFile(file) {
      if (file.status === 'ready') { //file.status==='ready'表示添加文件
        //判断文件类型是否为video
        if (file.raw.type.indexOf('video') === -1) {
          this.$message.warning('文件格式不对，请上传视频文件！');
          this.$refs.upload.clearFiles();
        } else {
          var video = file.raw;
          var url = URL.createObjectURL(video);
          var VideoObj = new Audio(url);
          const that = this;

          //监听视频预览错误，说明浏览器不支持这种视频格式的播放，无法读取时长，所以不能上传
          VideoObj.onerror = function(res) {
            that.$message.warning('视频格式不对，web端暂时不支持上传这种视频格式！');
            that.$refs.upload.clearFiles();
          }

          //监听视频能够播放，读取视频长度，将文件添加到fileList,等待上传
          VideoObj.oncanplaythrough = function() {
            var duration = parseInt(this.duration * 1000);
            that.videoParams.minute = duration;
            that.$refs.upload.submit();
          }
        }
      }
    },
    //上传本地文件失败
    uploadFail() {
      this.$message.error('上传失败！');
      this.$refs.upload.clearFiles();
    },
    //上传本地文件成功
    uploadSuccess(res) {
      console.log(res)
      if (res === 'already exists') {
        this.$message.warning('该视频文件已存在,请勿重复上传！');
      } else if (res === 'succeed') {
        this.$message.success('上传成功！');
      } else {
        this.$message.error('上传失败！');
      }
      this.$refs.upload.clearFiles();
    },

    //将主录放精灵的文件上传到公共网盘
    uploadFile(data) {
      var fileType = data.fileType === 'DMX' ? 0 : 1;
      var path = data.filePath;
      var md5 = data.md5;
      var time = data.time;
      spirit.uploadFile(fileType, path, md5, time).then(res => { console.log(res) })
    },

    //文件目录
    changePages(val) {
      this.page = val - 1;
    },

    //将文件添加到子任务
    handleAdd(data) {
      this.addFormVisible = true;
      this.addForm.filePath = data.filePath;
      this.addForm.time = parseInt(data.time);
      this.addForm.md5 = data.md5;
      this.addForm.type = data.fileType === 'DMX' ? 0 : 1;
    },

    //提交添加子任务
    addSubmit() {
      const that = this;
      if (this.addForm.subtaskName === '') {
        this.$message.warning('子任务名称不能为空');
      } else {
        addSubtask(this.addForm).then(res => {
          console.log(res)
          if (res.result === 'succeed') {
            that.addFormVisible = false;
            that.$message.success('添加成功！');
          } else if (res.result === 'already exists') {
            that.$message.warning('子任务名称已存在！');
          } else {
            that.$message.error('添加失败！');
          }
        })
      }
    },

    //分发文件
    sendVideoSplit(data) {
      var params = {
        mac: null,
        logInfo: '分发文件',
        ids: data.uploadId
      }
      if (data.fileType === 'DMX') {
        sendEdlmxSplit(params).then(res => { console.log(res) })
      } else if (data.fileType === 'VIDEO') {
        sendVideoSplit(params).then(res => { console.log(res) })
      }
    }
  },
  created() {
    this.getFileList();
  }
}

</script>
<style></style>



