<template>
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true">
        <el-form-item>
          <el-input placeholder="列表名"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary">查询</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!--列表-->
    <el-table :data="subtasksList" highlight-current-row style="width: 100%;">
      <el-table-column type="selection"></el-table-column>
      <el-table-column type="index"></el-table-column>
      <el-table-column label="子任务名称" prop="subtaskName"></el-table-column>
      <el-table-column label="创建时间" prop="createTime">
        <template slot-scope="scope">{{scope.row.createTime | dateFormat}}</template>
      </el-table-column>
      <el-table-column label="源文件名称" prop="filePath">
        <template slot-scope="scope">{{scope.row.filePath | fileName}}</template>
      </el-table-column>
      <el-table-column label="文件类型" prop="fileType">
        <template slot-scope="scope">{{scope.row.fileType | taskType}}</template>
      </el-table-column>
      <el-table-column label="播放时长" prop="time">
        <template slot-scope="scope">{{scope.row.time | timeFormat}}</template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.row)">编辑</el-button>
          <el-button size="mini" type="danger">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-button type="danger">批量删除</el-button>
      <el-pagination layout="prev, pager, next" :page-size="20" :total="subtasksList.length" style="float:right;"></el-pagination>
    </el-col>
    <!-- 编辑界面 -->
    <el-dialog title="编辑子任务" :visible.sync="editFormVisible" :close-on-click-modal="false" width="500px">
      <el-form :model="editForm">
        <el-form-item label="修改子任务名称">
          <el-input v-model="editForm.subtaskName" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="editSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </section>
</template>
<script>
import { getPlayList, editSubtask } from '@/api/api.js'

export default {
  data() {
    return {
      editFormVisible: false,
      editForm: {
        subtaskName: '',
        subtaskId: ''
      }
    }
  },
  computed: {
    subtasksList: function() {
      return this.$store.state.subtasksList;
    }
  },
  methods: {
    //获取子任务列表
    getSubtaskList() {
      const that = this;
      getPlayList().then(res => {
        console.log(res)
        that.$store.commit('updateSubtasksList', res.subtasksList);
      })
    },

    //显示编辑界面
    handleEdit(data) {
      this.editFormVisible = true;
      this.editForm.subtaskName = data.subtaskName;
      this.editForm.subtaskId = data.subtaskId;
    },
    editSubmit() {
      const that = this;
      if (this.editForm.subtaskName !== '') {
        editSubtask(this.editForm).then(res => {
          console.log(res)
          if (res.result === 'succeed') {
            that.getSubtaskList();
            that.$message.success('修改成功!');
            that.editFormVisible = false;
          }else if(res.result==='already exists'){
            that.$message.warning('此子任务名已存在!');
          }
        })
      } else {
        this.$message.warning('子任务名称不能为空！');
      }
    }
  },
  created() {
    this.getSubtaskList();
  }
}

</script>
<style></style>



