import { getFileList, uploadFile } from '@/api/api.js'

/**
 * 录放精灵
 * @param  {string} mac  录放精灵的mac地址
 */
class Spirit {
  constructor( mac) {
    this.mac = mac
  }

  /**
   * 获取文件文件列表
   * @return {object} 录放精灵的文件列表  status说明：0--未上传；1--已上传，未分发；2--已分发
   */
  getFileLists() {
    var params = {}
    return getFileList(params);
  }

  /**
   * 将录放精灵上未上传的文件上传到公共网盘
   * @param  {number} type 文件类型：0--灯光文件；1--视频文件
   * @param  {string} log  操作日志
   * @param  {string} path 文件路劲
   * @param  {string} md5  文件md5值，为文件的唯一标识
   * @param  {number} time 文件时长，单位为ms
   * @return {object}      上传反馈
   */
  uploadFile(type, path, md5, time) {
    var params = {
      fileType: type,
      md5: md5,
      filePath: path,
      time: parseInt(time)
    }
    return uploadFile(params);
  }
}

export default Spirit



