<template>
  <section>
    <el-col :span="24" class="toolbar">
      <el-form :inline="true">
        <el-form-item prop="date">
          <el-date-picker v-model="date" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-select v-model="status" placeholder="处理状态">
            <el-option v-for="item in statusOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-select v-model="type" placeholder="警报类型">
            <el-option v-for="item in typeOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="querySubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <el-col :span="8">
      <pieEcharts :pieOptionData="pieOptionData"></pieEcharts>
    </el-col>
    <el-col :span="16">
      <lineEcharts :date="dateList" :optionData="dataList"></lineEcharts>
    </el-col>
    <warningList :list="showList"></warningList>
    <el-col :span="24" class="toolbar">
      <el-pagination layout="prev, pager, next" :page-size="10" :total="alarmList.length" @current-change="changePage" @pre-click="changePage" @next-click="changePage" style="float:right;">
      </el-pagination>
    </el-col>
  </section>
</template>
<script>
import pieEcharts from '@/components/pieChart'
import lineEcharts from '@/components/lineChart'
import warningList from '@/components/warningList'
import { queryWarnningList } from '@/api/api.js'

export default {
  data() {
    return {
      date: '',
      type: '',
      status: '',
      statusOptions: [{
        value: 1,
        label: '已处理'
      }, {
        value: 0,
        label: '未处理'
      }, {
        value: '',
        label: '全部'
      }],
      typeOptions: [{
        value: 'RTR',
        label: '服务器'
      }, {
        value: 'Sprite',
        label: '录放精灵'
      }, {
        value: 'DMX512',
        label: '控制盒'
      }, {
        value: '',
        label: '全部'
      }],
      page: 0,
      dateList:[],  //折线图的x轴数据
      dataList:[]   //折线图的y轴数据
    }
  },
  computed: {
    alarmList: function() {
      return this.$store.state.alarmList;
    },
    showList: function() {
      return this.alarmList.slice(this.page * 10, (this.page + 1) * 10);
    },
    pieOptionData: function() {
      var spiritNum = 0;
      var rtrNum = 0;
      var controlBoxNum = 0;
      var currentNum = 0;
      var voltageNum = 0;
      for (var i = 0; i < this.alarmList.length; i++) {
        switch (this.alarmList[i].deviceType) {
          case 'Sprite':
            spiritNum++;
            break;
          case 'RTR':
            rtrNum++;
            break;
          case 'DMX512':
            controlBoxNum++;
            break;
          case 'Current':
            currentNum++;
            break;
          case 'Voltage':
            voltageNum++;
            break;
          default:
            return false;
            break;
        }
      }
      var data = [
        { value: spiritNum, name: '录放精灵' },
        { value: rtrNum, name: '服务器' },
        { value: controlBoxNum, name: '控制盒' },
        { value: currentNum, name: '电流' },
        { value: voltageNum, name: '电压' }
      ]
      return data;
    }
  },
  components: {
    pieEcharts,
    lineEcharts,
    warningList
  },
  methods: {
    changePage(num) {
      this.page = parseInt(num - 1);
    },
    querySubmit() {
      const that = this;
      console.log(this.date)
      if (this.date) {
        var params = {
          startTime: new Date(this.date[0]).getTime() / 1000,
          endTime: new Date(this.date[1]).getTime() / 1000,
          deviceType: this.type,
          resolutionState: this.status
        }
        queryWarnningList(params).then(res => {
          console.log(res);
          that.$store.state.alarmList = res.errList;
          that.page = 0;
          that.createLineData(res.alarmDateList,new Date(that.date[0]).getTime(),new Date(that.date[1]).getTime())
        })
      } else {
        this.$message.warning('请选择起止日期');
      }
    },
    createLineData(list,startTime,endTime) {
      var data={};
      for (var i = 0; i < list.length; i++) {
        data[list[i].alarmDate]=list[i].alarmNumber
      }
      var date=startTime;
      var dateList=[];
      var dataList=[];
      while(date<=endTime){
        var time=new Date(date).toLocaleDateString();
        var num=data[date]?data[date]:0;
        dateList.push(time);
        dataList.push(num);
        date+=86400000;
      }
      this.dateList=dateList;
      this.dataList=dataList;
    }
  }
}

</script>
<style></style>



