<template>
	<el-row>
		<el-col :span="24" class="currrent"></el-col>
		<el-col :span="24" class="voltage"></el-col>
		<el-col :span="24" class="temperature"></el-col>
	</el-row>
</template>

<script>
//引入Echarts主模块
import echarts from 'echarts/lib/echarts'
//引入折线图、饼状图等
import 'echarts/lib/chart/line'

//引入提示框和标题组件
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import "echarts/lib/component/legend"

export default {
  data() {
    return {
      option1: {
        title: {
          text: '  设备电流分析：2018年08日01日',
          left: 'center',
          textStyle: {
            fontSize: 14,
            fontWeight: 'normal'
          },
          padding: 13
        },
        legend: {
          bottom: '2%',
          data: ['设备电流']
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c}'
        },
        xAxis: {
          type: 'category',
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          },
          data: ['00：00', '02：00', '04：00', '06：00', '08：00', '10：00', '12：00', '14：00', '16：00', '18：00', '20：00', '22：00']
        },
        yAxis: {
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          },
          scale: true
        },
        grid: {
          top: '13%',
          left: '3%',
          right: '4%',
          bottom: '15%',
          containLabel: true
        },
        series: [{
          name: '设备电流',
          type: 'line',
          data: [1.1, 1.0, 1.3, 1.0, 1.0, 1.3, 1.0, 1.0, 1.0, 1.0, 1.2, 1.0],
          symbol: 'circle',
          symbolSize: 10,
          lineStyle: { normal: { color: '#ff6b6b' } }
        }]
      },
      option2: {
        title: {
          text: '  设备电压分析：2018年08日01日',
          left: 'center',
          textStyle: {
            fontSize: 14,
            fontWeight: 'normal'
          },
          padding: 13
        },
        legend: {
          bottom: '2%',
          data: ['设备电压']
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c}'
        },
        xAxis: {
          type: 'category',
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          },
          data: ['00：00', '02：00', '04：00', '06：00', '08：00', '10：00', '12：00', '14：00', '16：00', '18：00', '20：00', '22：00']
        },
        yAxis: {
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          },
          scale: true
        },
        grid: {
          top: '13%',
          left: '3%',
          right: '4%',
          bottom: '15%',
          containLabel: true
        },
        series: [{
          name: '设备电压',
          type: 'line',
          data: [230, 228, 228, 226, 228, 235, 224, 238, 236, 236, 235, 222],
          symbol: 'circle',
          symbolSize: 10,
          lineStyle: { normal: { color: '#ff6b6b' } }
        }]
      },
      option3: {
        title: {
          text: '  设备温度分析：2018年08日01日',
          left: 'center',
          textStyle: {
            fontSize: 14,
            fontWeight: 'normal'
          },
          padding: 13
        },
        legend: {
          bottom: '2%',
          data: ['设备温度']
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c}'
        },
        xAxis: {
          type: 'category',
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          },
          data: ['00：00', '02：00', '04：00', '06：00', '08：00', '10：00', '12：00', '14：00', '16：00', '18：00', '20：00', '22：00']
        },
        yAxis: {
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          },
          scale: true
        },
        grid: {
          top: '13%',
          left: '3%',
          right: '4%',
          bottom: '15%',
          containLabel: true
        },
        series: [{
          name: '设备温度',
          type: 'line',
          data: [32, 35, 33, 33, 34, 36, 40, 42, 42, 41, 42, 38],
          symbol: 'circle',
          symbolSize: 10,
          lineStyle: { normal: { color: '#ff6b6b' } }
        }]
      }
    }
  },
  methods: {
    //绘制折线图
    drawLine(className,data) {
      this.echartsLine = echarts.init(document.getElementsByClassName(className)[0])
      this.echartsLine.setOption(data)
    }
  },
  mounted() {
    this.drawLine('currrent',this.option1)
    this.drawLine('voltage',this.option2)
    this.drawLine('temperature',this.option3)
  }
}
</script>

<style scoped>
.currrent,.voltage,.temperature{
	height:300px;
}
</style>


