<template>
  <section class="wrap">
    <div class="login">
      <h4 class="title">雷 舞 中 控 系 统</h4>
      <div class="login-box">
        <div class="login-text">登 录</div>
        <el-form :model="loginForm" status-icon label-width="72px" label-position="left">
          <el-form-item label="账 号">
            <el-input type="text" v-model="loginForm.userName"></el-input>
          </el-form-item>
          <el-form-item label="密 码">
            <el-input type="password" v-model="loginForm.password"></el-input>
          </el-form-item>
          <el-checkbox v-model="loginForm.remember">记住密码</el-checkbox>
          <el-button type="primary" class="btn" @click="login">登 录</el-button>
        </el-form>
        <div class="logo-text">雷 舞 智 能</div>
      </div>
    </div>
  </section>
</template>
<script>
// 引入base64
const Base64 = require('js-base64').Base64;
import { login } from '@/api/api.js';

export default {
  data() {
    return {
      loginForm: {
        userName: '',
        password: '',
        remember: false
      }
    }
  },
  methods: {
    login() {
      login(this.loginForm).then(res => {
        console.log(res)
        /**
         * 0--超级用户
         * 1--普通用户
         * 2--账号不存在
         * 3--密码错误
         * 4--账号已禁用
         * 5--错误
         */
        switch (res.flag) {
          case '0':
            localStorage.setItem('token', JSON.stringify({ data: res.account, expires: new Date().getTime() + 24 * 60 * 60 * 1000 }))
            this.$router.push({ path: '/' });
            this.setUserInfo();
            break;
          case '1':
            break;
          case '2':
            this.$message.error('账号不存在');
            break;
          case '3':
            this.$message.error('密码错误');
            break;
          case '4':
            this.$message.warning('账号已被禁用');
            break;
          default:
            this.$message.error('未知错误');
            break;
        }
      })
    },

    //保存登录信息
    setUserInfo() {
      // 判断用户是否勾选记住密码，如果勾选，向cookie中储存登录信息，
      // 如果没有勾选，储存的信息为空
      if (this.loginForm.remember) {
        this.setCookie("userName", this.loginForm.userName,100000)
        // base64加密密码
        let password = Base64.encode(this.loginForm.password)
        this.setCookie("password",password,100000)
      } else {
        this.setCookie("userName", "")
        this.setCookie("password", "")
      }
    },

    //获取cookie
    getCookie(key) {
      var name = key + "=";
      var ca = document.cookie.split(';');
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i].trim();
        if (c.indexOf(name) == 0) return c.substring(name.length, c.length);
      }
      return "";
    },

    //保存cookie
    setCookie(key, value, exdays) {
      var d = new Date();
      d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
      var expires = "expires=" + d.toGMTString();
      document.cookie = key + "=" + value + "; " + expires;
    }
  },
  created() {
    // 在页面加载时从cookie获取登录信息
    let userName = this.getCookie("userName")
    let password = Base64.decode(this.getCookie("password"))
    // 如果存在赋值给表单，并且将记住密码勾选
    if (userName) {
      this.loginForm.userName = userName
      this.loginForm.password = password
      this.loginForm.remember = true
    }
  }
}

</script>
<style scoped>
.wrap {
  width: 100%;
  height: 100%;
  background-image: url(../assets/login-bg.png);
  background-size: cover;
  position: relative;
}

.login {
  position: absolute;
  width: 445px;
  right: 254px;
  bottom: 220px;
}

.login .title {
  height: 128px;
  line-height: 128px;
  font-size: 44px;
  color: #fff;
  text-align: center;
}

.login-box {
  background: #f3e5e7;
  border-radius: 10px;
  padding: 28px 82px 5px 53px;
}

.login-box>form {
  border-bottom: 1px solid #f6f6f6;
}

.login-text {
  height: 50px;
  line-height: 50px;
  font-size: 24px;
  color: #666;
  text-align: center;
  margin-bottom: 44px;
}

.login .btn {
  width: 100%;
  margin: 36px auto;
}

.login .logo-text {
  height: 65px;
  line-height: 65px;
  font-size: 22px;
  color: #c8c6c6;
  text-align: center;
  border-top: 1px solid #e8e8e8;
}

</style>



