<template>
  <div class="echarts-line"></div>
</template>
<script>
//引入Echarts主模块
import echarts from 'echarts/lib/echarts';
//引入折线图
import 'echarts/lib/chart/line';
//引入提示框和标题组件
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
import 'echarts/lib/component/legend';

export default {
  computed: {
    option: function() {
      var option = {
        title: {
          text: '  设备报警时间分析',
          left: 'center',
          textStyle: {
            fontSize: 14,
            fontWeight: 'normal'
          },
          padding: 13
        },
        legend: {
          bottom: '2%',
          data: ['设备报警']
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c}'
        },
        xAxis: {
          type: 'category',
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          }
        },
        yAxis: {
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(43,46,56,0.8)'
            }
          },
          scale: true
        },
        grid: {
          top: '13%',
          left: '3%',
          right: '4%',
          bottom: '15%',
          containLabel: true
        },
        series: [{
          name: '设备报警',
          type: 'line',
          symbol: 'circle',
          symbolSize: 10,
          lineStyle: { normal: { color: '#ff6b6b' } },
          smooth:true
        }]
      }
      option.xAxis.data = this.date;
      option.series[0].data = this.optionData;
      return option;
    }
  },
  props: ['date', 'optionData'],
  methods: {
    //绘制折线图
    drawLine() {
      this.echartsLine = echarts.init(document.getElementsByClassName('echarts-line')[0])
      this.echartsLine.setOption(this.option)
    },
    echartsResize() {
      const that = this
      this.echartsLine.resize()
      window.addEventListener('resize', function() {
        that.echartsLine.resize()
      })
    }
  },
  mounted() {
    this.drawLine()
    this.echartsResize()
  },
  watch: {
    option: function() {
      this.drawLine()
      this.echartsResize()
    }
  }
}

</script>
<style></style>



