import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    alarmList: [],
    deviceList: [],
    fileList: [],
    subtasksList: [],
    ifUpdateTimedTaskList: false,
    warningInfoList:[],
    ifUpdateWarningList:false
  },
  mutations: {
    initList(state, payload) {
      state.alarmList = payload.alarmDataJson;
      state.deviceList = payload.deviceDataJson;
      state.warningInfoList=payload.alarmDataJson;
    },
    updateFileList(state, payload) {
      state.fileList = payload;
    },
    updateSubtasksList(state, payload) {
      state.subtasksList = payload;
    },
    updateTimedTaskList(state, payload) {
      state.ifUpdateTimedTaskList = payload;
    }
  }
})



