<template>
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true">
        <el-form-item>
          <el-input placeholder="机架名"></el-input>
        </el-form-item>
        </el-form-item>
        <el-form-item>
          <el-button type="primary">查询</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!--列表-->
    <el-table :data="showList" highlight-current-row style="width: 100%;">
      <el-table-column prop="rackIndex" label="机架编号"></el-table-column>
      <el-table-column prop="rackName" label="机架名"></el-table-column>
      <el-table-column prop="sonRtrDeviceList[0].isOnline" label="服务器"></el-table-column>
      <el-table-column prop="sonRtrDeviceList[0].dmxOfflineAlarmStatus" label="控制盒"></el-table-column>
      <el-table-column prop="sonSpriteDeviceList[0].isOnline" label="录放精灵"></el-table-column>
      <el-table-column prop="current" label="电流（A）"></el-table-column>
      <el-table-column prop="voltage" label="电压（V）"></el-table-column>
    </el-table>
    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination layout="prev, pager, next" :page-size="10" :total="deviceList.length" @current-change="changePage" @pre-click="changePage" @next-click="changePage" style="float:right;">
      </el-pagination>
    </el-col>
  </section>
</template>
<script>
export default {
  data() {
    return {
        page:0
    }
  },
  computed:{
    deviceList:function(){
        return this.$store.state.deviceList.sonRackDeviceList;
    },
    showList:function(){
        return this.deviceList.slice(this.page*10,(this.page+1)*10);
    }
  },
  methods:{
    changePage(num){
        this.page=parseInt(num-1);
    }
  },
  created(){
    console.log(this.$store.state.deviceList.sonRackDeviceList[0])
  }
}

</script>
<style></style>



