<template>
  <baidu-map class="baidu-map" :center="center" :zoom="zoom" map-type="BMAP_NORMAL_MAP" @ready="handler" scroll-wheel-zoom :map-click="false">
    <!-- 地图类型控件 -->
    <bm-map-type :map-types="['BMAP_NORMAL_MAP', 'BMAP_HYBRID_MAP']" anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-map-type>
    <!-- 标记点 -->
    <bm-marker v-for="item in markers" :position="{lng:item.rackLongitude,lat:item.rackLatitude}" :icon="{url: markerIcon,size:{width:64,height:64}}" ></bm-marker>
  </baidu-map>
</template>
<script>
export default {
  data() {
    return {
      center: { lng: 0, lat: 0 },
      zoom: 3,
      markerIcon:'data:image/png;base64,'
    }
  },
  computed:{
    markers:function(){
      return this.$store.state.deviceList.sonRackDeviceList;
    }
  },
  methods: {
    handler({ BMap, map }) {
      this.center.lng = 121.2909553997
      this.center.lat = 31.0746431711
      this.zoom = 20
    }
  }
}

</script>
<style>
.baidu-map {
  width: 100%;
  height:calc(100% - 30px);
  position: relative;
}

/*隐藏百度地图logo*/
.anchorBL {
  display: none;
}

</style>



