<template>
  <el-menu :default-active="currentRoute" class="left-navbar" background-color="#eef1f6" router>
    <el-menu-item index="/" route="/">
      <i class="el-icon-info"></i>
      <span slot="title">首页</span>
    </el-menu-item>
    <el-menu-item index="/device" route="/device">
      <i class="el-icon-info"></i>
      <span slot="title">设备状态</span>
    </el-menu-item>
    <el-menu-item index="/monitor" route="/monitor">
      <i class="el-icon-view"></i>
      <span slot="title">实时监控</span>
    </el-menu-item>
    <el-submenu index="/spirit">
      <template slot="title">
        <i class="el-icon-menu"></i>
        <span>录放精灵</span>
      </template>
      <el-menu-item index="/fileList" route="/fileList">文件列表</el-menu-item>
      <el-menu-item index="/playList" route="/playList">子任务列表</el-menu-item>
      <el-menu-item index="/taskList" route="/taskList">任务列表</el-menu-item>
    </el-submenu>
    <el-menu-item index="ele" route="/ele">
      <i class="el-icon-news"></i>
      <span slot="title">智能电气设备</span>
    </el-menu-item>
    <el-menu-item index="/switch" route="/switch">
      <i class="el-icon-news"></i>
      <span slot="title">强电管理</span>
    </el-menu-item>
    <el-menu-item index="/warning" route="/warning">
      <i class="el-icon-bell"></i>
      <span slot="title">警报管理</span>
    </el-menu-item>
  </el-menu>
</template>
<script>
export default {
  data(){
    return {
      currentRoute:''
    }
  },
  created() {
    this.currentRoute = this.$router.currentRoute.fullPath
  }
}

</script>
<style>
ul.left-navbar {
  height: 100%;
}

</style>



