<template>
  <el-table :data="list" :max-height="maxHeight" :size="size" border highlight-current-row>
    <el-table-column align="center" type="index" label="序号"></el-table-column>
    <el-table-column align="center" prop="startTime" label="时间">
      <template slot-scope="scope">{{scope.row.startTime | dateFormat}}</template>
    </el-table-column>
    <el-table-column align="center" prop="rackName" label="机柜"></el-table-column>
    <el-table-column align="center" prop="deviceType" label="警报类型"></el-table-column>
    <el-table-column align="center" prop="errDescribe" label="警报内容"></el-table-column>
    <el-table-column align="center" prop="errRank" label="警报级别">
        <template slot-scope="scope"><el-tag :type="scope.row.errRank==='Grave'?'danger':scope.row.errRank==='General'?'warning':''">{{scope.row.errRank | warningLevel }}</el-tag></template>
    </el-table-column>
    <el-table-column align="center" prop="resolutionState" label="状态">
      <template slot-scope="scope">{{scope.row.resolutionState==0?'未解除':'已解除'}}</template>
    </el-table-column>
    <el-table-column align="center" prop="endTime" label="解除时间">
      <template slot-scope="scope">{{scope.row.endTime | dateFormat}}</template>
    </el-table-column>
    <el-table-column align="center" label="操作">
      <template slot-scope="scope">
        <el-button type="primary" size="small" v-if="scope.row.errType==='Lmx' || scope.row.errType==='Task' ||  scope.row.errType==='Video'" @click="recover(scope.row)">恢复</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
import { sendLoseEdlmxFileSpilt, sendLoseVideoFileSpilt, sendLoseTimedTask } from '@/api/api.js'

export default {
  data() {
    return {

    }
  },
  filters: {
    warningLevel:function(value){
        if(value==='General'){
            return '普通';
        }else if(value==='Grave'){
            return '严重';
        }
    }
  },
  props: ['list', 'maxHeight', 'size'],
  methods: {
    recover(obj) {
      if (obj.errType === 'Lmx') {
        sendLoseEdlmxFileSpilt({ fileMd5: obj.typeMd5 }).then(res => { console.log(res) })
      } else if (obj.errType === 'Video') {
        sendLoseVideoFileSpilt({ fileMd5: obj.typeMd5 }).then(res => { console.log(res) })
      } else if (obj.errType === 'Task') {
        sendLoseTimedTask({ taskName: obj.taskName }).then(res => { console.log(res) })
      }
    }
  }
}

</script>
<style></style>



