<template>
  <section>
    <el-table :data="gatewayList">
      <el-table-column type="index"></el-table-column>
      <el-table-column align="center" label="网关名称" prop="rackName"></el-table-column>
      <el-table-column align="center" label="网关IP" prop="ip"></el-table-column>
      <el-table-column align="center" label="在线状态">
        <template slot-scope="scope">
          <el-button type="success" circle v-if="scope.row.isOnline===1"></el-button>
          <el-button type="danger" circle v-if="scope.row.isOnline===0"></el-button>
        </template>
      </el-table-column>
      <el-table-column align="center" label="自动模式">
        <template slot-scope="scope">
          <el-switch v-model="scope.row.mode" active-color="#13ce66" inactive-color="#ff4949" @change="changeMode"></el-switch>
        </template>
      </el-table-column>
      <el-table-column align="center" label="继电器模块">
        <template slot-scope="scope">
          <el-button type="text" @click="$router.push({path:'/relay',query:{mac:scope.row.mac,rackId:scope.row.rackId}})">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-button type="primary" @click="$router.push({path:'/nodeList'})">添加任务</el-button>
      <el-button type="primary" @click="getGatewayInstantTask">快捷操作</el-button>
    </el-col>
    <!-- 快捷操作列表 -->
    <el-dialog title="快捷操作" :visible.sync="instantTaskVisible" center>
      <el-row :gutter="40">
        <el-col style="padding-bottom:20px" :span="6" v-for="item in instantTaskList" :key="item.taskName">
          <el-button style="width:100%" type="primary" @click="quickActions(item.taskData)">{{item.taskName}}</el-button>
        </el-col>
      </el-row>
    </el-dialog>
  </section>
</template>
<script>
import { getGatewayList, getGatewayInstantTask, sendInstantTask, sendTimerTask } from '@/api/api.js'

export default {
  data() {
    return {
      gatewayList: [], //网关列表
      instantTaskVisible: false, //是否显示快捷操作界面
      instantTaskList: [] //已保存的即时任务（快捷操作）列表
    }
  },
  methods: {
    //获取智能网关列表
    getGatewayList() {
      const that = this;
      getGatewayList().then(res => {
        if (res.result === 'true') {
          console.log(res)
          that.gatewayList = res.intelligentGatewayList;
        }
      })
    },

    //点击快捷操作，显示已保存的即时任务列表
    getGatewayInstantTask() {
      const that = this;
      this.instantTaskVisible = true;
      getGatewayInstantTask().then(res => {
        if (res.result === 'true') {
          that.instantTaskList = res.realtimeTasksList
        }
      })
    },

    //点击快捷操作按钮，发送即时任务
    quickActions(direction) {
      var params = JSON.parse(direction);
      this.sendInstantTask(params)
    },

    //开启/关闭自动模式
    changeMode(){

    },

    //发送即时任务
    sendInstantTask(params) {
      const that = this;
      sendInstantTask(params).then(res => {
        if (res.result === 'send successful') {
          that.$message.success('发送成功！');
        }
      })
    },

    //发送定时任务
    sendTimerTask(params) {
      sendTimerTask(params).then(res => { console.log(res) })
    },

    //开启自动模式（发送一个空的定时任务）
    openAutoMode(rackId) {
      var params = {
        rackId: rackId,
        taskMsg: {}
      }
      this.sendTimerTask(params);
    },

    //关闭自动模式（发送一个空的即时任务）
    closeAutoMode(rackId) {
      var params = {
        rackId: rackId,
        taskMsg: []
      }
      this.sendInstantTask(params);
    }
  },
  created() {
    this.getGatewayList();
  }
}

</script>
<style></style>



