<template>
	<div>
		<el-button circle v-for="item in list" :type="item?'success':'danger'"></el-button>
	</div>
</template>
<script>
export default {
  props: ['onList', 'offList'],
  computed: {
    list: function() {
      var trueList = this.onList.split(',');
      var falseList = this.offList.split(',');
      var arr = [];
      for (var i = 0; i < trueList.length; i++) {
        arr[parseInt(trueList[i])] = true;
      }
      for (var i = 0; i < falseList.length; i++) {
        arr[parseInt(falseList[i])] = false;
      }
      return arr;
    }
  }
}

</script>
<style></style>



