<template>
  <div class="box-wrap">
    <div class="box" :style="{width:width,height:height}">
      <div class="left-top-radius"></div>
      <div class="right-bottom-radius"></div>
      <div class="left-top-cover"></div>
      <div class="right-bottom-cover"></div>
      <slot></slot>
    </div>
  </div>
</template>
<script>
export default {
  props: ['width', 'height']
}

</script>
<style scoped>
.box-wrap {
  width: 100%;
  padding: 10px 15px;
  box-sizing: border-box;
}

.box {
  width: 100%;
  height: 100%;
  border: 1px solid #2eb1d2;
  position: relative;
  background-image: url(../assets/box_bg.png);
  background-position: 6px 30px;
  background-repeat: no-repeat;
  box-sizing: border-box;
  padding: 10px 20px 20px 40px;
}

.left-top-radius,
.right-bottom-radius,
.left-top-cover,
.right-bottom-cover {
  width: 0;
  height: 0;
  position: absolute;
  border-style: solid;
}

.left-top-radius {
  border-width: 21px;
  border-color: #2eb1d2 transparent transparent #2eb1d2;
  left: -1px;
  top: -1px;
}

.left-top-cover {
  border-width: 20px;
  border-color: #fff transparent transparent #fff;
  left: -1px;
  top: -1px;
}

.right-bottom-radius {
  border-width: 21px 18px;
  border-color: transparent #2eb1d2 #2eb1d2 transparent;
  right: -1px;
  bottom: -1px;
}

.right-bottom-cover {
  border-width: 20px 17px;
  border-color: transparent #fff #fff transparent;
  right: -1px;
  bottom: -1px;
}

</style>



