<template>
  <div class="echarts-pie"></div>
</template>
<script>
//引入Echarts主模块
import echarts from 'echarts/lib/echarts';
//引入饼状图
import 'echarts/lib/chart/pie';
//引入提示框和标题组件
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
import 'echarts/lib/component/legend';

export default {
  computed: {
    option: function() {
      var option = {
        tooltip: {
          trigger: 'item',
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },
        title: {
          text: '  警报管理',
          left: 'left',
          textStyle: {
            fontSize: 14,
            fontWeight: 'normal'
          },
          padding: 13
        },
        legend: {
          orient: 'vertical',
          right: '2%',
          top: '5%',
          data: ['录放精灵', '服务器', '控制盒', '电压', '电流'],
        },
        series: [{
          name: '警报类型',
          type: 'pie',
          radius: ['35%', '70%'],
          center: ['35%', '50%'],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: 'center'
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: '20',
                fontWeight: 'bold',
                // color: '#fff'
              }
            }
          }
        }]
      }
      option.series[0].data = this.pieOptionData;
      return option;
    }
  },
  props: ['pieOptionData'],
  methods: {
    //绘制饼状图
    drawPie() {
      this.echartsPie = echarts.init(document.getElementsByClassName('echarts-pie')[0])
      this.echartsPie.setOption(this.option)
    },
    echartsResize() {
      const that = this
      this.echartsPie.resize()
      window.addEventListener('resize', function() {
        that.echartsPie.resize()
      })
    }
  },
  mounted() {
    this.drawPie()
    this.echartsResize()
  },
  watch: {
    option: function() {
      this.drawPie()
      this.echartsResize()
    }
  }
}

</script>
<style></style>



